.header-color {
background: #141E30;  /* fallback for old browsers */
background: -webkit-linear-gradient(to right, #243B55, #141E30);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to right, #243B55, #141E30); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
}.landing-grid {
background: #005AA7;  /* fallback for old browsers */
background: -webkit-linear-gradient(to left, #FFFDE4, #005AA7);  /* Chrome 10-25, Safari 5.1-6 */
background: linear-gradient(to left, #FFFDE4, #005AA7); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
text-align: center;

top: 0;
left: 0;
width: 100%;
height: 100%;
}
.projects-grid {
display: flex;}
/* Resume Page css */
.resume-right-col {
background: #27221F;
color: white;
padding: 2em;
}
.resume-grid {
text-align: center;
justify-content: center;
width: 80%;
height: 550px;
background: white;
color: black;
margin-bottom: 20px;
}
/* contact page css */
.contact-body {
margin: auto;
position: fixed;
width: 100%;
height: 100%;
justify-content: center;
padding-top: 2em;
}
.contact-grid {
text-align: center;
justify-content: center;
width: 80%;
height: 550px;
background: white;
color: black;
margin-bottom: 20px;
}
.contact-grid h2 {
font-family: 'Anton', sans-serif;
}.contact-grid p {
font-family: 'Oxygen', sans-serif;
font-weight: bold;
}.contact-grid hr {
border-top: 5px dotted black;
width: 50%;
margin: auto;
}.contact-list i {
font-size: 66px;
padding-right: 5rem;
}
